/* @include loader_style; */
@mixin loader_style
{
	#woob2b_loader{
			position:fixed;
			display:block;
			width:100%;
			height:100%;
			top:0;
			left:0;
			z-index:1000;
			background:#fff url(../img/spinners/spinner.gif) center center no-repeat;
			background-size:150px auto;
			opacity:0.80;
	}
}